import React from 'react';
import {motion} from 'framer-motion';

import {pageTransitionProp,pageVariantsProp} from '../../shared/Utility';
import classes from './Skills.module.css';
import skillsSvg from '../../assets/images/skills.png';
 

const Skills = ()=>{

     const skillObj={
        
        skillsSummary:[
            'Experienced in Front end development using HTML, JavaScript, ReactJs, Redux, React router '+
            'and Animations. I have created single page and Responsive applications',
            'Worked extensively with Java 8, JPA, JSF, Spring boot, Spring JDBC and Jersey REST Services'+
            ' to create enterprise applications',
            'Mostly used Maven to build Java projects , but have exposure to Gradle and Ant as well.',
            'Worked with XSLTs to transform XMLs.',
            'Created REST APIs with Spring boot and Oracle REST Data Services. '+
            'I have worked with data intensive systems, So many of the REST services had paginated response.',
            'Exposed the REST Web services on  WSO2 API Gateway.',
            'While working with Oracle REST Data Services, I have acquired good knowledge of pl/sql,'+
            ' Query optimization and Oracle JSON_OBJECT and JSON_ARRAY',
            'Used Jenkins for CI/CD.',
            'Have worked in Agile and Waterfall model. currently working in an Agile team'
        ]
            

    };

    let skillSummary=skillObj.skillsSummary.map( (summary,index) =>
                <li key={index}>{summary}</li>
        );
        return(
            <motion.div initial="initial" animate="in" exit="out" variants={pageVariantsProp}
            transition={pageTransitionProp}>
          <div className={classes.content}>

                <div className={classes.contentItem}>
                        <img  src={skillsSvg} alt ='skill'></img>
                </div>
                <div className={classes.contentItem}>
                        <div className={classes.card}>
                            <ul>
                                {skillSummary}
                            </ul>
                        </div>
                </div>

            </div>
     
            </motion.div>
        );
    
}
export default Skills;