import React from 'react';
import {motion} from 'framer-motion';

import {pageTransitionProp,pageVariantsProp} from '../../shared/Utility';
import classes from './Experience.module.css';
import manLaptopSvg from '../../assets/images/man_laptop.png';

const Experience = ()=>{

    const expSummary='The cards highlight some of the projects that I have worked'+
                ' on and my current project.'+
                ' Hover or Click (For mobile) on card to see the details';
   
   const  experience={
        professionalSummary:[
          {
              heading:'Project 1',
              desc:'I  started my carrer with a gaming company where I worked as a Unity3D game developer'+
              ' and I was responsible for the following',
              bulletPoints:[
                'implementation of the core logic of game (using C# Scripting)', 
                'using NGUI package for unity to develop UI part of Game',
                'Integrating graphics and Sound in game',
                'Adding Facebook APIs to game (To post highscores and creating friends leader board)',
                'As part of development team we were also involved in discussions with graphics team to determine overall look and feel of game'
                

              ]
          },
          {
            heading:'Project 2',
            desc:'The project was to enhance the intranet portal of client where they used to publish'+
            ' all the relevant details of organization.'+
            ' The portal was created using polopoly content management system.',
            bulletPoints:[
                'Created new XML templates for article creation in CMS.',
                'Worked on SOLR to optimize the search performance and make the search more effective.',
                'Responsible for creating JSP views.',
                'Created REST Web service using jersey.',
                'Used JIRA for tracking the progress of the task.',
                'Involved in requirement discussion with client and providing the efforts estimates.'
            ]
        },
        {
            heading:'Project 3',
            desc:'The system is used to calculate the compensation to be given'+
            ' to different stores based on their sales during a promotional period.',
            bulletPoints:[
                'Created UI using JSF framework.',
                'Worked with JMS queues.',
                'Implemented code to handle XML contracts using JAXB which were further processed and stored in database using JPA. ',
                'Developed EJB timers to clean historic data from system.',
                'Responsible for adding JUnit test cases and logging.',
                'Presented demo to the client at regular intervals.'
            ]
        },
        {
            heading:'Project 4',
            desc:'The project included rewriting the UI layer in Angular 4'+
            ' to overcome the performance bottlenecks of GWT and hence making application smooth and scalable.',
            bulletPoints:[
                'Responsible for creating UI screens using Angular ',
                'Worked on different aspects of Angular like Components, Services, Template driven and Reactive forms, Directives, Routing, HTTP Services etc. ',
                'Consuming oauth2 Secured web services.',
                'Responsible for code reviews (peer) and fixing bugs found while testing.'
            ]
        },
        {
            heading:'Current Project',
            desc:'The system stores all the relevant data of retail landscape like Item details'+
            ', Retail Price, promotional offers etc. and provide these details to different'+
            ' stores based on the request.',
            bulletPoints:[
                'I served as a senior developer for this project and was involved in discussions with the team on overall architecture of the project and the technology stack. ',
                'Involved in database design and responsible for modifying the database objects.',
                'Creating Jenkins job for build automation. ',
                'Built UI using React (Using routing, redux etc.)',
                'Built rest web services in spring boot and Oracle Rest data services.',
                'Extensively worked with java collections, stream API and XPATH expressions to extract data from XML contracts.',
                'Responsible for creating persistence layer using Spring JDBC.'
            ]
        }

        ]
    };
    let professionalSummary=experience.professionalSummary.map((summary,index)=>
    <div key={index} className={classes.card}>
           <h3 className={classes.headingFont}>{summary.heading}</h3>
           <hr></hr>
           <div className={classes.desc}>{summary.desc}</div>
           <ul>
             {
               summary.bulletPoints.map((point,indexIn)=>{
               return <li key={indexIn} className={classes.bulletDesc}>{point}</li>
               })

             }
               
           </ul>
    </div>
);



        // const isMobile = window.innerWidth < 850;
        // const imagWidth=isMobile?350:400;
      
        return(
            <motion.div initial="initial" animate="in" exit="out" variants={pageVariantsProp}
            transition={pageTransitionProp}>
  
                <div  className={classes.wrapper}>
                        
                        <div className={classes.wrapperItem}>
                            <img  src={manLaptopSvg} alt="manLaptop"></img>
                            <p>{expSummary}</p>
                        </div>
                    
                        <div className={classes.wrapperItem}>
                            <div  className={classes.cards}>
                                {professionalSummary}
                            </div>
                        </div>
                       
                        
                </div>
                
                
            </motion.div>
        );
    }
  


export default Experience;