export const pageVariantsProp={
    initial:{
        opacity:0,
        x:'-200vh',
        scale:0
    },
    in:{
        opacity:1,
        x:'0',
        scale:1
    },
    out:{
        opacity:0,
        x:'200vh',
        scale:0
    }
}


export const pageTransitionProp={
    type:'tween',
    ease:"anticipate",
    duration:1
}